.pg-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
    max-width: 60%;
    margin: 0 auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  .pg-align-center {
    -ms-flex-align: center;
        align-items: center;
  }
  
  /* Header */
  
  header {
    position: relative;
  }
  
  header nav {
    position: fixed;
    padding: 10px 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
  }
  
  header nav ul.pg-navigation {
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  header nav ul.pg-navigation li a {
    display: block;
    padding: 0 16px;
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
  }
  
  header nav ul.pg-navigation li:last-child a {
    padding-right: 0;
  }
  
  header nav ul.pg-navigation li a:after {
    content: '';
    display: block;
    height: 1px;
    width: 0;
    background-color: #fff;
    opacity: 0;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
  }
  
  header nav ul.pg-navigation li a:hover:after {
    opacity: 1;
    width: 100%;
  }
  
  .pg-header-text {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    height: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background: rgba(0, 0, 0, .5);
  }
  
  .pg-main-banner {
    display: block;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 0.7)));
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
    background: -o-linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
    background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
    width: 100%;
  }
  
  .pg-header-text .pg-arrow {
    display: inline-block;
    padding: 20px;
    border: solid #fff;
    border-width: 0 3px 3px 0;
    cursor: pointer;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
        transform: rotate(45deg);
    -webkit-transition: opacity .3s ease;
    -o-transition: opacity .3s ease;
    transition: opacity .3s ease;
  }
  
  .pg-header-text .pg-arrow:hover {
    opacity: .7;
  }
  
  
  /* Logo */
  
  a.pg-logo {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 60px;
    height: 60px;
    color: #fff;
    font-size: 26px;
    text-decoration: none;
  }
  
  /* Sections  */
  
  .pg-section {
    padding: 85px 0;
  }
  
  /* About us */
  
  #pg-about .pg-column {
    width: 40%;
    margin-top: 25px;
    text-align: center;
  }
  
  /* Animation */
  
  .pg-pulsate {
    -webkit-animation: pulsate 3s ease-in-out;
    -webkit-animation-iteration-count: infinite;
    opacity: .3;
  }
  
  @-webkit-keyframes pulsate {
    0% {
      opacity: .3;
    }
    50% {
      opacity: 1;
    }
    100% {
      opacity: .3;
    }
  }


